@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: white;
  font-family: "Open Sans", sans-serif;
}

*:focus {
  outline: none;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  background-color: #17151c;
}

/* Spinner */
.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #2e2e2e;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spin 0.5s linear infinite;
          animation: spin 0.5s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Loading */
.loading {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.notfound {
  text-align: center;
  margin: 50px 0;
}

/* ScrollBard */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #262b2f;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(240, 240, 240);
}

/* Container */
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.search-header {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-header .search-logo img {
  width: 150px;
}

.search-header ul {
  display: flex;
}

.search-header ul li {
  margin: 0 10px;
}

.search-header {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.search-page form {
  display: flex;
  justify-content: center;
  width: 500px;
  margin: 50px auto;
}
.search-page form input {
  padding: 10px;
  border: 0;
  border-top-left-radius: 5px;
  background-color: #2d2d2d;
  border-bottom-left-radius: 5px;
  width: 100%;
}

.search-page form button {
  padding: 10px;
  border: 0;
  border-top-right-radius: 5px;
  background-color: #232121;
  border-bottom-right-radius: 5px;
}

.search-page form button:hover {
  cursor: pointer;
}

/* Card */
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.search-card {
  max-width: 250px;
  border-radius: 10px;
  overflow: hidden;
  background-color: #272231;
  margin: 10px 0;
}

.search-card .cover img {
  width: 250px;
}

.search-card .title {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 0.9em;
}

.search-card .card-detail {
  padding: 5px 10px;
}

@media (min-width: 768px) {
  .search-header {
    width: 750px;
  }
}

@media (max-width: 600px) {
  .search-page form {
    width: 80vw;
  }
}

/* Paggination */

.paggination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.paggination ul {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
}

.paggination ul li {
  padding: 10px 15px;
  background-color: #272231;
}

.paggination ul li:hover {
  cursor: pointer;
  background-color: #1a1620;
}

.paggination ul .current {
  background-color: #ea5252;
}

.info {
  font-size: 0.875rem;
}

#course {
  display: flex;
  max-width: 1366px;
  height: 100vh;
  overflow: hidden;
}

.course-detail {
  width: 70vw;
  overflow-y: scroll;
}

.course-sections {
  width: 30vw;
  background-color: #413f56;
}

.course-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px auto;
  width: 70%;
}

.course-header .course-logo img {
  width: 150px;
}

.course-header ul {
  display: flex;
}

.course-header ul li {
  margin: 0 10px;
}

/* Course Detail */

.course {
  width: 70%;
  margin: auto;
}

.video {
  width: 100%;
  border-radius: 20px;
  margin: 20px 0;
}

.course-headline {
  margin: 20px 0;
}

.grid {
  display: grid;
  grid-template-columns: 200px auto;
  padding: 20px 0;
  border-bottom: 1px solid white;
}

.course-numbers div {
  display: flex;
  justify-content: space-between;
}

.course-desc {
  border: none;
}

.course-desc p {
  margin: 10px 0;
}

.course-desc ul {
  list-style: disc;
  padding-left: 20px;
  margin: 10px 0;
}

.no-curriculum {
  margin: 50px 0;
}

.no-curriculum i {
  color: #ea5252;
}

/* ScrollBard */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #262b2f;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(240, 240, 240);
}

.course-sections {
  overflow-y: scroll;
}

.section-wrapper {
  width: 80%;
  margin: 50px auto;
}

.section {
  margin: 20px 0;
  border-bottom: 1px solid white;
  padding-bottom: 20px;
}

.section ul li {
  border: 1px solid;
  border-radius: 3px;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
}

.section ul li p {
  width: 70%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.section ul li i {
  color: white;
  transition: color 0.2s;
}

.section ul li i:hover {
  cursor: pointer;
  color: #ea5252;
}

